<template>
  <div style="display: none;">
    <!-- 这是一个无渲染组件，只用于管理结构化数据 -->
  </div>
</template>

<script>
export default {
  name: 'StructuredData',
  props: {
    type: {
      type: String,
      default: 'WebSite' // WebSite, Article, SoftwareApplication
    },
    title: {
      type: String,
      default: 'Flyio.js Documentation'
    },
    description: {
      type: String,
      default: 'Flyio.js 是一个轻量级、功能强大的 JavaScript HTTP 请求库'
    },
    url: {
      type: String,
      default: 'https://flyio-js.vercel.app/'
    },
    image: {
      type: String,
      default: 'https://flyio-js.vercel.app/static/v.png'
    },
    author: {
      type: String,
      default: 'liusheng22'
    },
    publishDate: {
      type: String,
      default: ''
    },
    modifiedDate: {
      type: String,
      default: ''
    }
  },
  mounted() {
    this.updateStructuredData()
  },
  watch: {
    type: 'updateStructuredData',
    title: 'updateStructuredData',
    description: 'updateStructuredData',
    url: 'updateStructuredData',
    image: 'updateStructuredData',
    author: 'updateStructuredData',
    publishDate: 'updateStructuredData',
    modifiedDate: 'updateStructuredData'
  },
  methods: {
    updateStructuredData() {
      // 移除旧的结构化数据
      const oldScript = document.querySelector('script[data-structured-data]')
      if (oldScript) {
        oldScript.remove()
      }

      let structuredData = {}

      if (this.type === 'WebSite') {
        structuredData = {
          '@context': 'https://schema.org',
          '@type': 'WebSite',
          'name': this.title,
          'description': this.description,
          'url': this.url,
          'image': this.image,
          'author': {
            '@type': 'Person',
            'name': this.author
          },
          'potentialAction': {
            '@type': 'SearchAction',
            'target': `${this.url}search?q={search_term_string}`,
            'query-input': 'required name=search_term_string'
          }
        }
      } else if (this.type === 'Article') {
        structuredData = {
          '@context': 'https://schema.org',
          '@type': 'Article',
          'headline': this.title,
          'description': this.description,
          'url': this.url,
          'image': this.image,
          'author': {
            '@type': 'Person',
            'name': this.author
          },
          'publisher': {
            '@type': 'Organization',
            'name': 'Flyio.js Documentation',
            'logo': {
              '@type': 'ImageObject',
              'url': 'https://flyio-js.vercel.app/static/v.png'
            }
          }
        }

        if (this.publishDate) {
          structuredData.datePublished = this.publishDate
        }
        if (this.modifiedDate) {
          structuredData.dateModified = this.modifiedDate
        }
      } else if (this.type === 'SoftwareApplication') {
        structuredData = {
          '@context': 'https://schema.org',
          '@type': 'SoftwareApplication',
          'name': 'Flyio.js',
          'description': this.description,
          'url': this.url,
          'image': this.image,
          'author': {
            '@type': 'Person',
            'name': this.author
          },
          'applicationCategory': 'DeveloperApplication',
          'operatingSystem': 'Cross-platform',
          'programmingLanguage': 'JavaScript',
          'license': 'MIT'
        }
      }

      // 添加新的结构化数据
      const script = document.createElement('script')
      script.type = 'application/ld+json'
      script.setAttribute('data-structured-data', 'true')
      script.textContent = JSON.stringify(structuredData, null, 2)
      document.head.appendChild(script)
    }
  }
}
</script>



